<template>
  <div>
    <div  style="margin-left:50px;">
      <h3>认证信息</h3>
      <div v-if="form.is_certification == 1" class="succ">已认证成功，不可修改，如若修改，请联系平台人员</div>
    	<div v-if="form.is_certification == 2" class="wei">未审核, 请填写信息提交审核</div>
    	<div v-if="form.is_certification == 3" class="error">审核失败，请修改后重新提交, 原因：{{ form.failure_why }}</div>
    	<div v-if="form.is_certification == 4" class="runing">审核中, 将会在2-3个工作日完成审核</div>
    </div>
    <div>
    	
      <el-form
        class="mt15"
        :status-icon="true"
        :model="form"
        :rules="rules"
        ref="form"
        label-width="120px"
        label-position="right"
      >
        <el-form-item label="企业名称" prop="enterprise_name">
            <el-col :span="8">
                <el-input v-model="form.enterprise_name" :disabled="isShow"></el-input>
            </el-col>
        </el-form-item>
        <el-form-item label="法人姓名" prop="person_name">
            <el-col :span="8">
                <el-input v-model="form.person_name" :disabled="isShow"></el-input>
            </el-col>
        </el-form-item>
        <el-form-item label="公司地址" prop="address">
          <el-select v-model="form.province" placeholder="请选择省" @change="provinceChange">
            <el-option v-for="(v,i) in province_list" :label="v.areaName" :value="v.areaId" :key="i" :disabled="isShow"></el-option>
          </el-select>
          <el-select v-model="form.city" placeholder="市" @change="cityChange"> 
            <el-option v-for="(v,i) in city_list" :label="v.areaName" :value="v.areaId" :key="i" :disabled="isShow"></el-option>
          </el-select>
          <el-select v-model="form.area" placeholder="区">
            <el-option v-for="(v,i) in area_list" :label="v.areaName" :value="v.areaId" :key="i" :disabled="isShow"></el-option>
          </el-select>
        </el-form-item>
		<el-form-item label="详细地址" prop="address">
            <el-col :span="8">
               <el-input
					  
					  placeholder="请输入公司地址"
					  v-model="form.address"
					  maxlength="30"
					  show-word-limit
					  :disabled="isShow"
					></el-input>
            </el-col>
        </el-form-item>
        <el-form-item label="营业执照" prop="license_photo">
            <el-col :span="8">
               <el-upload
                  action="http://39.96.29.15:9501/api/v1/upload_img"
                  list-type="picture-card"
                  name="file"
                  :show-file-list="true"
                  :on-preview="handlePictureCardPreview"
                  :on-remove="handleRemove"
                  :on-success="uploadImageSuccess"
                  :before-upload="beforeAvatarUpload"
                  :file-list="fileList"
                  :limit="1"
                  >
                  <i class="el-icon-plus"></i>
                </el-upload>
            </el-col>
            <el-col :span="10"></el-col>
        </el-form-item>

        <el-form-item label="社会信用码" prop="credit_num">
            <el-col :span="8">
                <el-input v-model="form.credit_num" :show-word-limit="true" :disabled="isShow"></el-input>
            </el-col>
        </el-form-item>
        <el-form-item label="身份证号" prop="person_id_num">
            <el-col :span="8">
                <el-input v-model="form.person_id_num" :disabled="isShow"></el-input>
            </el-col>
        </el-form-item>
        <el-form-item label="联系电话" prop="phone">
            <el-col :span="8">
                <el-input v-model="form.phone" :disabled="true"></el-input>
            </el-col>
        </el-form-item>
        <el-form-item label="联系邮箱" prop="email">
            <el-col :span="8">
                <el-input v-model="form.email" :disabled="isShow"></el-input>
            </el-col>
        </el-form-item>
        
        <el-form-item>
          <el-button type="primary" @click="submitForm('form')" :disabled="isShow">提交认证</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
// import mavonEditor from 'mavon-editor'
// import 'mavon-editor/dist/css/index.css' 
export default {
  name: "App",
  data() {
    return {
      isShow:false,
      province_list:[],
      city_list:[],
      area_list:[],
      fileList:[],
      form: {
        person_name: "", //法人
        license_photo:'', //营业执照
        enterprise_name: "", //公司名称
        credit_num: "", //信用号
        province: "", //省
        city: "", //市
        area:'', //区
        phone: '', 
        person_id_num: "", //法人身份证号
        email:'',
        address:'',
      },
      rules: {
        enterprise_name: [
          { required: true, message: "请输入企业名称", trigger: "blur" },
        ],
        person_name: [
          { required: true, message: "请输入法人名称", trigger: "blur" },
        ],
        province : [
            { required : true, message : '请选择省', trigger : "blur"}
        ],
        city : [
            { required : true, message : '请选择市', trigger : "blur"}
        ],
        area : [
            { required : true, message : '请选择区', trigger : "blur"}
        ],
        email : [
            { required : true, message : '请输入公司邮箱', trigger : "blur"}
        ],
        phone : [
            { required : true, message : '请输入公司电话', trigger : "blur"}
        ],

        credit_num : [
            { required : true, message : '请输入公司社会信用代码号', trigger : "blur"}
        ],
        person_id_num : [
            { required : true, message : '请输入法人身份证号', trigger : "blur"}
        ],
        address : [
            { required : true, message : '请输入公司地址', trigger : "blur"}
        ],
      }
    };
  },

  created () {

    this.getProvince();
    this.getInfo();
  },
  methods: {
  	getInfo()
  	{
  		this.$api.get('/enterprise/get_info',{}, res => {
  			this.form=res.data.data
  			this.fileList=[
  				{
  					name : '营业执照',
  					url : this.form.license_photo
  				}
  			];
  			this.isShow= this.form.is_certification == 2 || this.form.is_certification == 3 ? false : true
  		});
  	},
    beforeAvatarUpload(file) {
      const isJPG = file.type === 'image/jpeg';
      const isLt2M = file.size / 1024 / 1024 < 2;

      if (!isJPG) {
        this.$message.error('上传头像图片只能是 JPG 格式!');
      }
      if (!isLt2M) {
        this.$message.error('上传头像图片大小不能超过 2MB!');
      }
      return isJPG && isLt2M;
    },
    handleRemove(file, fileList){
      console.log('移除'+ file)
    },
    uploadImageSuccess(res, file, fileList)
    {
      this.form.license_photo=res.data.url;
    },
    handlePictureCardPreview(file){
      console.log(file)
    },


    getProvince(){
        let self = this;
    	this.$api.get('api/v1/get_province', {},res=>{
          self.province_list=res.data.data;
        });
    },
    provinceChange(e)
    {
        let self = this;
        this.$api.get('api/v1/get_area', {pid:e},res=>{
          self.city_list=res.data.data;
        });
    },
    cityChange(e){
    	let self = this;
        this.$api.get('api/v1/get_area', {pid:e},res=>{
          self.area_list=res.data.data;
        });
    },
    dateRes () {
        var d = new Date(this.form.end_time);  
        var  resDate = d.getFullYear() + '-' + (d.getMonth() + 1) + '-' + d.getDate() + ' ' + d.getHours() + ':' + d.getMinutes() + ':' + d.getSeconds(); 
        return resDate;
    },
    submitForm(formName) {
    	var self = this;
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.form.end_time=this.dateRes();
          console.log(this.form);
          this.$api.post('enterprise/certification', this.form, res=>{
            if(res.data.bol)
            {
              this.$message.success(res.data.msg);
              setTimeout(() => {
               	self.getInfo();
            }, 1500);
            }
          });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    }
  }
};
</script>

<style>
body {
  background: #f1f1f1;
}
	.succ , .error, .runing, .wei{color:#fff; padding: 5px 5px 5px 20px; border-radius: 5px;}
  .succ{background:#67C23A;}
  .error{background:#F56C6C}
  .runing{background:#909399}
  .wei{background:#E6A23C;}
</style>



